import Vue from 'vue'
import Router from 'vue-router'
import pcindex from '@/views/pc/index'

import pchome from '@/views/pc/home/index'
import fastNews from '@/views/pc/fastMessage/index'
import fastNewsDetail from '@/views/pc/fastMessage/detail'
import deepNews from '@/views/pc/deepMessage/index'
import deepNewsDetail from '@/views/pc/deepMessage/detail'
import specialTopic from '@/views/pc/specialTopic/index'
import specialTopicDetail from '@/views/pc/specialTopic/detail'
import information from '@/views/pc/information/index'
import projectDetail from '@/views/pc/information/projectDetail'
import organizationDetail from '@/views/pc/information/organizationDetail'
import navigation from '@/views/pc/navigation/index'
import search from '@/views/pc/search/index'
import columns from '@/views/pc/columns/index'

import h5index from '@/views/h5/index'

import h5home from '@/views/h5/home/index'
import h5fast from '@/views/h5/fast/index'
import h5fastDetail from '@/views/h5/fastDetail/index'
import h5deep from '@/views/h5/deep/index'
import h5deepDetail from '@/views/h5/deepDetail/index'
import h5topic from '@/views/h5/topic/index'
import h5information from '@/views/h5/information/index'
import h5columns from '@/views/h5/columns/index'
import h5projectDetail from '@/views/h5/projectDetail/index'
import h5organizationDetail from '@/views/h5/organizationDetail/index'
Vue.use(Router)

const pageRouter =  new Router({
  mode: 'history',
  routes: [
    {
      path: '/pc',
      name: 'PC首页',
      component: pcindex,
      redirect: '/pc/home',
      children:[
        {
          path: '',
          redirect: '/pc/home'
        },
        {
          path: 'home',
          name: 'pchome',
          component: pchome,
        },
        {
          path: 'fastNews',
          name: 'pcfastNews',
          component: fastNews,
        },
        {
          path: 'fastNewsDetail/:id',
          name: 'pcfastNews1',
          component: fastNewsDetail,
        },
        {
          path: 'deepNews',
          name: 'pcdeepNews',
          component: deepNews,
        },
        {
          path: 'deepNewsDetail/:id',
          name: 'pcdeepNews1',
          component: deepNewsDetail,
        },
        {
          path: 'specialTopic',
          name: 'pcspecialTopic',
          component: specialTopic,
        },
        {
          path: 'specialTopicDetail',
          name: 'pcspecialTopicDetail',
          component: specialTopicDetail,
        },
        {
          path: 'information',
          name: 'pcinformation1',
          component: information,
        },
        {
          path: 'projectDetail',
          name: 'pcinformation2',
          component: projectDetail,
        },
        {
          path: 'organizationDetail',
          name: 'pcinformation3',
          component: organizationDetail,
        },
        {
          path: 'navigation',
          name: 'pcnavigation',
          component: navigation,
        },
        {
          path: 'search/:value',
          name: 'pcsearch',
          component: search,
        },
        {
          path: 'columns',
          name: 'pccolumns',
          component: columns,
        }
      ]
    },
    {
      path: '/h5',
      name: 'h5index',
      component: h5index,
      redirect: '/h5/home',
      children:[
        {
          path: '',
          redirect: '/h5/home'
        },
        {
          path: 'home',
          name: 'h5home',
          component: h5home,
        },
        {
          path: 'fastNews',
          name: 'h5fast',
          component: h5fast,
        },
        {
          path: 'deepNews',
          name: 'h5deep',
          component: h5deep,
        },
        {
          path: 'specialTopic',
          name: 'h5topic',
          component: h5topic,
        },
        {
          path: 'information',
          name: 'h5information',
          component: h5information,
        },
      ]
    },
    {
      path: '/h5/fastDetail/:id',
      name: 'h5fastDetail',
      component: h5fastDetail,
    },
    {
      path: '/h5/deepDetail/:id',
      name: 'h5deepDetail',
      component: h5deepDetail,
    },
    {
      path: '/h5/columns/:id',
      name: 'h5columns',
      component: h5columns,
    },
    {
      path: '/h5/projectDetail/:id',
      name: 'h5projectDetail',
      component: h5projectDetail,
    },
    {
      path: '/h5/organizationDetail/:id',
      name: 'h5organizationDetail',
      component: h5organizationDetail,
    },
  ]
})
pageRouter.beforeEach((to, from, next) => {
  document.body.scrollTop = 0
  // firefox
  document.documentElement.scrollTop = 0
  next()
});
export default pageRouter;
