      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <!-- swiper -->
        <div class="swiperView">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item>
              <div class="swiperItem">
                <img src="./../../../assets/h5Image/newsImg.png" alt="">
              </div>
            </van-swipe-item>
            <van-swipe-item>
              <div class="swiperItem">
                <img src="./../../../assets/h5Image/newsImg.png" alt="">
              </div>
            </van-swipe-item>
            <van-swipe-item>
              <div class="swiperItem">
                <img src="./../../../assets/h5Image/newsImg.png" alt="">
              </div>
            </van-swipe-item>
            <van-swipe-item>
              <div class="swiperItem">
                <img src="./../../../assets/h5Image/newsImg.png" alt="">
              </div>
