          </van-swipe>
        </div>
        <!-- hot -->
        <div class="hotView">
          <div class="hotItem">
            <img src="./../../../assets/h5Image/toutiao.png" alt="" class="hotLogo">
            <p class="hotTxt">一周预告｜美联储公布12月货币政策会议纪要；SBF将于1月3日在纽约受审</p>
          </div>
          <div class="hotItem">
            <img src="./../../../assets/h5Image/toutiao.png" alt="" class="hotLogo">
            <p class="hotTxt">一周预告｜美联储公布12月货币政策会议纪要；SBF将于1月3日在纽约受审</p>
          </div>
        </div>
        <!-- list -->
        <div class="listView">
          <div v-for="(item,index) in articleList" :key="index">
            <articleItem  :info="item"/>
          </div>
        </div>
      </van-list>
