  },
  data() {
    return {
      tabList: [
        {name:'WEB',id: 1},{name:'NFT',id: 2},{name:'公链',id: 3},{name:'WEB',id: 4},
        {name:'DAO',id: 5},{name:'Metaverse',id: 6},{name:'X2E',id: 7},{name:'WEB',id: 8},
      ],
      list: [1,2,3,4,5,6],
      loading: false,
      finished: false,
      refreshing: false,
    };
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 20; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 60) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;
      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    clickTab(name, title) {
