<template>
  <div class="deep_h5_page">
    <div class="top_bar">
      <topInput />
    </div>
    <topTab :tabList="tabList"/>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="deep_list">
          <div class="deep_item" v-for="item in 6" :key="item" @click="$router.push('/h5/deepDetail/1')">
            <listItem :info="{title:'1confirmation合伙人：我对2023年Web3产品的3个趋势预测',content:'1月3日消息，据官方推特，以太坊扩容项目Scroll表示将于1月9日执行Pre-Alpha网络重置，提高Pre…Alpha测试网吞吐量。网络重置完…',img:require('./../../../assets/h5Image/newsImg.png')}"/>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
    
  </div>
</template>
<script>
import topInput from './../components/topInput';
import topTab from './../components/topTab'
import listItem from './component/listItem'
export default {
  name: "detail",
  components: {
    topInput,
    topTab,
    listItem
  },
  data() {
    return {
      tabList: [
        {name:'WEB',id: 1},{name:'NFT',id: 2},{name:'公链',id: 3},{name:'WEB',id: 4},
        {name:'DAO',id: 5},{name:'Metaverse',id: 6},{name:'X2E',id: 7},{name:'WEB',id: 8},
      ],
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
    };
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 20; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 60) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;
      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    clickTab(name, title) {
      console.log(name, title)
    },
  },
  mounted() {
    
  },
 
};
</script>
<style lang="less">
.deep_h5_page {
  padding-top: 52px;
  padding-bottom: 68px;
  .top_bar {
    width: 100%;
    height: 52px;
    background-size: 100% 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
  }
  
  .deep_list {
    padding-left: 16px;
    padding-right: 14px;
    .deep_item {

    }
  }
}
</style>
  