<template>
  <div id="pcView" >
    <div class="header_view">
      <img src="@/assets/images/logo.png" alt="logo" class="header_left" @click="$router.replace('/home')">
      <div class="header_mid">
        <!-- 锚链接区域 -->
        <p :class="tab==1 ? 'choose' : 'ontChoose'" @click="clickTab(1)">首页</p>
        <p :class="tab==2 ? 'choose' : 'ontChoose'" @click="clickTab(2)">快讯</p>
        <p :class="tab==3 ? 'choose' : 'ontChoose'" @click="clickTab(3)">深度</p>
        <p :class="tab==4 ? 'choose' : 'ontChoose'" @click="clickTab(4)">专题</p>
        <p :class="tab==5 ? 'choose' : 'ontChoose'" @click="clickTab(5)">项目</p>
        <p :class="tab==6 ? 'choose' : 'ontChoose'" @click="clickTab(6)">导航</p>
      </div>
      <div class="header_right">
        <input v-model="searchValue"/>
        <div class="search_btn" @click="goSearch">
          <img src="@/assets/images/search.png" alt="">
        </div>
      </div>
    </div>

    <div class="router_view">
      <transition name="fade">
        <router-view />  
      </transition>
    </div>

    <div class="footer_view">
      <div class="footer_top">
        <div class="footer_top_left">
          <div class="footer_webcn">
            <img src="@/assets/images/logo.png" alt="">
            <p>专业的区块链研究机构与资讯平台</p>
          </div>
          <p class="footer_intro">Web3CN是一个专业的web3信息服务平台，为用户提供及时、专业且有品质的web3资讯和数据，致力于让更多的人进入web3并捕捉到web3的市场机遇。</p>
          <div class="footer_link">
            <a><img src="@/assets/images/twitter.png" alt=""></a>
            <a><img src="@/assets/images/discord.png" alt=""></a>
            <a><img src="@/assets/images/msg.png" alt=""></a>
          </div>
        </div>
        <div class="footer_top_right">
          <img src="@/assets/images/code1.png" alt="">
          <img src="@/assets/images/code2.png" alt="">
        </div>
      </div>
      <div class="footer_bottom">
        <p>寻求报道</p>
        <p>隐私政策</p>
        <p>工作机会</p>
        <p>版权申明</p>
      </div>

      <div class="footer_icon">
        <div class="phone">
          <img src="@/assets/images/phoneicon.png" alt="" class="footer_fixedicon" @click="showDownload=!showDownload">
          <div class="download_view" v-show="showDownload">
            <div class="download_app">
              <img src="@/assets/images/ios.png" alt="">
              <p>IOS下载</p>
            </div>
            <div class="download_app">
              <img src="@/assets/images/ios.png" alt="">
              <p>安卓下载</p>
            </div>
          </div>
        </div>
        <div class="totop">
          <img src="@/assets/images/totopicon.png" alt="" class="footer_fixedicon">
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name: "index",
 
  data() {
    return {
      tab: 1,
      searchValue: '',
      showDownload: false
    }
  },
 
  watch: {
   
  },
  methods: {
    
    clickTab(val) {
      this.tab = val;
      let route = ''
      switch (val) {
        case 1:
          route = '/pc/home'
          break;
        case 2:
          route = '/pc/fastNews'
          break;
        case 3:
          route = '/pc/deepNews'
          break;
        case 4:
          route = '/pc/specialTopic'
          break;
        case 5:
          route = '/pc/information'
          break;
        case 6:
          route = '/pc/navigation'
          break;
        default:
          break;
      }
      this.$router.push(route)
    },
    getRoute() {
      let routeName = this.$route.name;
      if(routeName.includes('pcsearch')) { // 
        routeName = localStorage.getItem('ROUTE_NAME') || 'pchome';
      }

      if(routeName.includes('pchome')) {
        this.tab = 1;
      }
      if(routeName.includes('pcfastNews')) {
        this.tab = 2;
      }
      if(routeName.includes('pcdeepNews')) {
        this.tab = 3;
      }
      if(routeName.includes('pcspecialTopic')) {
        this.tab = 4;
      }
      if(routeName.includes('pcinformation1')) {
        this.tab = 5;
      }
      if(routeName.includes('pcnavigation')) {
        this.tab = 6;
      }

      if(routeName.includes('pccolumns')) {
        this.tab = 7;
      }
      
    },
    goSearch() {
      if(this.searchValue) {
        let routeName = this.$route.name;
        localStorage.setItem('ROUTE_NAME',routeName)
        this.$router.push('/pc/search/'+this.searchValue)
      }
    }
  },
  mounted() {
   
    this.getRoute();
    // this.linkWallet();
  },
  created() {
    // this.langSelectValue = this.$i18n.locale;
  }
  
};
</script>

<style lang="less">
#pcView {
  min-height: 100vh;
  .header_view {
    width: 100%;
    height: 3.9rem;
    background: linear-gradient(135deg, #191919 0%, #F0B200 100%);
    box-shadow: 0px 2px 4px 0px #EEEEEE;
    padding: 0 7.65rem 0 6.5rem;
    display: flex;
    align-items: center;
    .header_left {
      width: 100px;
      margin-right: 4.4rem;
      flex-shrink: 0;
    }
    .header_mid {
      flex: 1;
      display: flex;
      align-items: center;
      p  {
        font-size: 1rem;
        font-weight: 600;
        line-height: 1.2rem;
        margin-right: 3.45rem;
        cursor: pointer;
      }
      .ontChoose {
        color: #ffffff;
      }
      .choose {
        color: #F0B200;
      }
    }
    .header_right {
      width: 24rem;
      height: 2.55rem;
      background: rgba(255,255,255,0.2);
      border-radius: 1.4rem;
      margin-right: 0.5rem;
      display: flex;
      align-items: center;
      padding: 0 1.15rem;
      input {
        flex: 1;
        height: 2rem;
        border: none;
        outline: none;
        font-size: 0.8rem;
        text-align: left;
        background: none;
        color: #444;
        caret-color: #F0B200;
      }
      .search_btn {
        flex-shrink: 0;
        width: 2rem;
        height: 2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        img {
          width: 1.34rem;
          height: auto;
        }
      }
    }
  }
  .router_view {
    flex: 1;
    width: 100%;
  }
  .footer_view {
    width: 100%;
    min-height: 16.25rem;
    padding: 2.4rem 8.15rem 1.25rem 6.55rem ;
    background: linear-gradient(135deg, #191919 0%, #F0B200 100%);
    .footer_top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 0.85rem;
      border-bottom: 1px solid #CECABE;
      .footer_top_left {
        .footer_webcn {
          display: flex;
          align-items: center;
          margin-bottom: 1rem;
          img {
            width: auto;
            height: 1.96rem;
            margin-right: 2.45rem;
          }
          p {
            font-size: 1.25rem;
            font-weight: 600;
            color: #FFFFFF;
            padding: 0.4rem 0;
            padding-left: 0.5rem;
            border-left: 1px solid #CECABE;
          }
        }
        .footer_intro {
          width: 31.15rem;
          font-size: 0.7rem;
          font-weight: 600;
          color: #9C9C9C;
          line-height: 1.2rem;
          margin-bottom: 1.6rem;
        }
        .footer_link {
          display: flex;
          align-items: center;
          a {
            width: 3rem;
            height: 3rem;
            margin-right: 2rem;
            cursor: pointer;
            &:last-child {
              margin-right: 0;
            }
          }
          img {
            width: 100%;
            height: 100%;
          }
        }

      }
      .footer_top_right {
        display: flex;
        align-items: center;
        img {
          width: 7rem;
          height: 7rem;
          margin-right: 2rem;
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
    .footer_bottom {
      margin-top: 1rem;
      display: flex;
      align-items: center;
      p {
        font-size: 0.8rem;
        font-weight: 500;
        color: #9C9C9C;
        margin-right: 1.5rem;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  .footer_icon {
    position: fixed;
    bottom: 2rem;
    right: 1rem;
    .footer_fixedicon {
      width: 2.75rem;
      height: 2.75rem;
      cursor: pointer;
    }
    .phone {
      position: relative;
      margin-bottom: 2rem;
    }
    .download_view {
      position: fixed;
      right: 4.2rem;
      bottom: 4.7rem;
      width: 13.75rem;
      height: 7.2rem;
      background: linear-gradient(180deg, #F1B800 0%, #FEFBE5 100%);
      border-radius: 0.75rem;
      display: flex;
      justify-content: center;
      img {
        width: 6rem;
        height: 6rem;
      }
      p {
        font-size: 1rem;
        font-weight: 600;
        color: #444444;
        text-align: center;
      }
    }
    
  }


  .fade-enter-active {
    transition: all 0.3s ease;
  }
  .fade-leave-active {
    transition: all 0.3s ease;
  }
  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }
}
</style>
