        position: "top-left",
        customClass: "fastMsg_page_el_notification",
        showClose: false,
      });
      this.notifyObj["notify" + item.id] = notifyObj;
    },
    closeShare() {
      this.showShare = false;
    },
    closeNotify(index) {
      this.notifyObj["notify" + index] &&
        this.notifyObj["notify" + index].close();
