<template>
  <div class="org_h5">
    <topTab :tabList="tabList"/>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="org_h5_list">
          <div class="org_h5_item" v-for="item in 6" :key="item" @click="$router.push('/h5/organizationDetail/1')">
            <div class="org_h5_item_top">
              <div class="org_h5_name">
                <img src="./../../../assets/h5Image/newsImg.png" alt="">
                <p>TEFDJKLSA</p>
              </div>
            </div>
            <div class="org_h5_item_bottom">
              <p>投资项目数：132</p>
              <div>
                <img src="./../../../assets/images/detailTwitterIcon.png" alt="">
                <img src="./../../../assets/images/detailWebIcon.png" alt="">
              </div>
            </div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>

    
  </div>
</template>
<script>
import topTab from './../components/topTab'
export default {
  name: "detail",
  components: {
    topTab
  },
  data() {
    return {
      tabList: [
        {name:'WEB',id: 1},{name:'NFT',id: 2},{name:'公链',id: 3},{name:'WEB',id: 4},
      ],
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
    };
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 20; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 60) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;
      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    clickTab(name, title) {
      console.log(name, title)
    },
  },
  mounted() {
    
  },
 
};
</script>
<style lang="less">
.org_h5 {
  .van-tab--active {
    .van-tab__text--ellipsis {
      padding: 2px 8px;
      border-radius: 15px;
      background: #F0B200;
      color: #fff;
    }
  }
  .org_h5_list {
    padding: 0 15px;
    .org_h5_item {
      width: 100%;
      border: 1px solid #EDEDED;
      border-radius: 19px;
      margin-bottom: 14px;
      overflow: hidden;
      .org_h5_item_top {
        padding-left: 13px;
        padding-right: 19px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 62px;
        border-bottom: 1px solid #EDEDED;
        background: #FAFAFA;
        .org_h5_name {
          display: flex;
          align-items: center;
          img {
            width: 38px;
            height: 38px;
            border-radius: 100%;
          }
          p {
            font-size: 13px;
            font-family: PingFangSC-Semibold, PingFang SC;
            font-weight: 600;
            color: #000000;
            margin-left: 12px;
          }
        }
      }
      .org_h5_item_bottom {
        width: 100%;
        height: 62px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left: 13px;
        padding-right: 19px;
        p {
          font-size: 13px;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-weight: 600;
          color: #9C9C9C;
        }
        div {
          display: flex;
          img {
            width: 24px;
            height: 24px;
            cursor: pointer;
            &:last-child {
              margin-left: 9px;
            }
          }
        }
      }
    }
  }
  
}
</style>
  