<template>
  <div class="columns_h5_page">
    <van-sticky>
      <van-nav-bar
        title="DEfI之道"
        left-arrow
        @click-left="$router.go(-1)"
      />
    </van-sticky>
    <div class="columns_h5_content">
      <div class="columns_h5_logo">
        <img src="./../../../assets/h5Image/newsImg.png" alt="">
      </div>
      <p class="columns_h5_name">DEfI之道<span>132篇文章</span></p>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="deep_list">
          <div class="deep_item" v-for="item in 6" :key="item" @click="$router.push('/h5/deepDetail/1')">
            <listItem :info="{title:'1confirmation合伙人：我对2023年Web3产品的3个趋势预测',content:'1月3日消息，据官方推特，以太坊扩容项目Scroll表示将于1月9日执行Pre-Alpha网络重置，提高Pre…Alpha测试网吞吐量。网络重置完…',img:require('./../../../assets/h5Image/newsImg.png')}"/>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>
<script>
import 'swiper/dist/js/swiper'
import 'swiper/dist/css/swiper.css'
import listItem from './../deep/component/listItem'
export default {
  name: "detail",
  components: {
    listItem
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
    };
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 20; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 60) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;
      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    
  },
  mounted() {
    
  },
 
};
</script>
<style lang="less">
.columns_h5_page {
  .van-nav-bar .van-icon {
    color: #444444;
    font-size: 22px;
  }
  .van-nav-bar__title{
    font-size: 17px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #444444;
  }
  .columns_h5_content {
    padding: 19px 16px 19px 14px;
    .columns_h5_logo {
      margin: 0 auto;
      width: 58px;
      height: 58px;
      background: #FFFFFF;
      box-shadow: 0px 2px 4px 0px rgba(238,238,238,1);
      border-radius: 100%;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 10px;
      img {
        width: 50%;
        height: 50%;
      }
    }
    .columns_h5_name {
      margin-bottom: 29px;
      text-align: center;
      font-size: 17px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #444444;
      span {
        font-size: 13px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        color: #9C9C9C;
        padding-left: 10px;
      } 
    }
    
  }
}
</style>
  