<script>
import API from "@/assets/js/API";
export default {
  name: "App",
  data() {
    return {
      
    }
  },
  
  mounted() {
    const url = window.location.origin;
    const href = window.location.href;
    const param = href.split('?')[1] || '';
    if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
      
      if(!href.includes('h5')) {
        window.location.href = `${url}/h5/home${param ? '?'+param :''}`
      }
      
    } else {
      if(!href.includes('pc')) {
        window.location.href = `${url}/pc/home${param ? '?'+param :''}`
      } else {
        
      }
     
    }

  },
  methods: {
  
  }
};
</script>

